.overview_container {
  flex-basis: 60%;
}

.rainbow {
  color: black;
  background-color: orange;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.highlight_dark {
  background: #D33668;
  color: #ffffff;
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 600;
  line-height: 26.4px;
}

.highlight_light {
  background: #0E0F1C;
  color: #D33668;
  border: 1px solid #516391;
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 600;
  line-height: 26.4px;
}

@media all and (max-width: 900px) {
  .overview_container {
    flex-basis: 100%;
    align-self: center;
  }
}